<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                专家智库详情
            </p>
        </section>
        <section class="middle">
            <div class="figure">
                <img :src="detail.pic" alt="">
            </div>
            <p class="article-title" v-text="detail.name"></p>
            <p class="collect" @click="collect">
                <span v-if="detail.isCollection">已收藏</span>
                <span v-else>收藏</span>
            </p>
        </section>
        <hr>
        <section class="bottom">
            <div class="title red-line">专家类别</div>
            <div class="article" v-html="detail.type">
            </div>
            <div class="title blue-line">研究领域</div>
            <div class="article" v-html="detail.genre">
            </div>
            <div class="title red-line">主要成果</div>
            <div class="article" v-html="detail.achievements">
            </div>
            <div class="title blue-line">荣誉资质</div>
            <div class="article" v-html="detail.socialHonor">
            </div>
            <div class="title red-line">典型案例</div>
            <div class="article" v-html="detail.classicCase">
            </div>
            <div class="title blue-line">附件下载</div>
            <div class="link" @click="download" v-text="detail.fileName">文件名</div>
        </section>
        <div class="button" @click="docking">
            <img src="@/assets/home/icon_xuqiuduijie@2x.png" alt="">
            专家对接
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/technology/img@2x.png" alt="">
            <p>对接信息已发送,</p>
            <p>工作人员将会在24小时内联系您!</p>
        </md-dialog>
    </main>
</template>
<script>
import { getDetail } from '@/apiUrl/expert'
import { docking, collect } from '@/apiUrl/technology'
import { Icon, Toast, ActivityIndicator, Landscape, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      showMaskClosable: false,
      currentId: '',
      detail: {},
      iconDialog: {
        open: false
      }
    }
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    download () {
      window.open(this.detail.file)
    },
    docking () {
        this.$router.push({ path: '/submission/index' ,query: { type : 4 } })
      // docking({ type: 4, infoId: this.currentId }).then(res => {
      //   // this.showMaskClosable = true
      //   this.iconDialog.open = true
      // }).catch(err => {
      //   Toast.failed(err)
      // })
    },
    // 收藏
    collect () {
      const args = {
        infoId: this.currentId,
        type: 4
      }
      collect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
