            width 690px
            .cover
                background rgba(255, 255, 255, 1)
                box-shadow: 1px 1px 8px 0px rgba(2, 25, 85, 0.2)
                border-radius 6px
                padding 20px
                margin-bottom 40px
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 24px
                        color #666666
                        flex 0 0 50%
                        text-align left
                        margin 20px auto
                        padding-left 36px
                    li:nth-child(odd)
                        background url("../../../assets/technology/item_blue@2x.png") left center no-repeat
                        background-size 26px 26px
                    li:nth-child(even)
                        background url("../../../assets/technology/item_red@2x.png") left center no-repeat
                        background-size 26px 26px
            .title
                width 690px
                height 66px
                line-height 66px
                color #333333
                font-size 28px
                text-indent 30px
