                margin-bottom 30px
            .blue-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_blue@2x.png") left center no-repeat
                background-size 13px 66px
            .link
                font-size 24px
                color #021955
                padding-left 18px
                text-align left
                margin-bottom 52px
        .button
            position fixed
            bottom 0
            display: flex
            align-items center
            justify-content center
            width 100%
            height 98px
            background rgba(229,232,238,1);
            font-size 32px
            box-shadow 0px 2px 12px 0px rgba(2,25,85,0.36);
            img
                width 26px
                height 42px
                margin-right 19px
    .md-landscape-content
        padding 30px
        font-size 30px
        line-height 1.5
        color #666
        p
            margin-bottom 30px
    /deep/.md-dialog-body
                display: flex;
                justify-content: center;
                flex-direction: column;
                align-items: center;
                text-align center
                img
                    width 80%
